.profile {
  min-height: 100vh;
  grid-template-columns: 64px 1fr;
  margin: 0;
  padding: 0;
  display: grid;
}

.profile__main {
  list-style: none;
}

.profile__form {
  text-align: center;
  max-width: 510px;
  margin: 60px auto 0;
  font-family: inherit;
}

.profile__user-name {
  color: #1e1e1e;
  text-align: center;
  margin: 0 0 60px;
  font-size: 15px;
  font-weight: 600;
  line-height: 15px;
}

.profile__list {
  margin: 0 0 60px;
  padding: 0;
}

