.profile {
   margin: 0;
   padding: 0;
   min-height: 100vh;
   display: grid;
   grid-template-columns: 64px 1fr;
 
   &__main {
     list-style: none;
   }
 
 
   &__form {
     font-family: inherit;
     text-align: center;
     margin-top: 60px;
     max-width: 510px;
     margin: 60px auto 0;
   }
 
   &__user-name {
     margin: 0;
     font-size: 15px;
     color: #1e1e1e;
     font-weight: 600;
     line-height: 15px;
     text-align: center;
     margin-bottom: 60px;
   }
 
   &__list {
     margin: 0;
     padding: 0;
     margin-bottom: 60px;
   }
 
  
 }
 